import React, {useState, useEffect} from 'react';
import { SubmitButton } from '../components/Button';
import ReusableForm from '../components/FormTemplate';
import { initialValues, fields } from '../resources/forms/sdohContent';
//import { useNavigate } from 'react-router-dom';
//import { TextField } from '@mui/material';
//import { formatDate } from '../utils/formatDate';

// TEST PATIENT IN M502:  MRN= 2952535   FIN= 29525350001

function UtSdoh() {   

    //const [currentPage, setCurrentPage] = useState('landingpage');
    const [isLoading, setIsLoading] = useState(true);
    //const navigate = useNavigate();
    

    //const initialValues = { mrn: '', fin: '', foodSecurity: ''};
    /* const fields = [
        { name: 'mrn', label: 'MRN', type: 'text' },
        { name: 'fin', label: 'FIN', type: 'text' },
        { name: 'foodSecurity', label: 'Have you been worried about food running out before you could afford to buy more in the past 12 months?', 
        type: 'radio', // Specify the field type as 'radio'
        options: [ // Specify options for the radio button field
            { value: 'Often True', label: 'Often True' },
            { value: 'Sometimes True', label: 'Sometimes True' },
            { value: 'Never True', label: 'Never True' },
            ],
        },
    ];
    */   
    
    const handleSubmit  = (values) => {
        /*
        /// Format the date value before submission
        const formattedValues = {
            ...values,
            dob: formatDate(values.dob),
        };*/

        fetch('https://uhsvtsdohdapp01.utmck.edu:5000/api/submit', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(values)
        })
        .then((response) => response.json())
        .then((data) => {
            console.log(data);
            console.log(values);

        })
        .catch(error => {
            // Handle errors
            console.error(error);
        });
    };

    useEffect(() => {
        window.addEventListener('load', () => {
        setIsLoading(false);
        });
        return () => {
        window.removeEventListener('load', () => {});
        };
    }, []);

    
    

    return (
        <div>
            <h1>This is the SDOH FORM Page - v0.27</h1>
            <ReusableForm
                initialValues={initialValues}
                onSubmit={handleSubmit}
                fields={fields}
                SubmitButton={(props) => (
                    <SubmitButton {...props} text="SUBMIT" />
                )}
            />
        </div>
    );
}

export default UtSdoh;